<template>
	<view class="mapDetail">
		<view class="map_inner">
			<map
				@markertap="markertap"
				:polygons="polygons"
				:markers="markers"
				class="map"
				v-bind:style="{ height: serviceHeight + 'px' }"
				show-location
				scale="16"
				id="map"
				:latitude="latitude"
				:longitude="longitude"
			></map>
			<!-- <view class="myPerson_location" @click="clickCurrentLocation">
				<image src="../../static/icon/weizhi.png" mode=""></image>
				<text>当前定位</text>
			</view> -->
			
		</view>
	</view>
</template>

<script>
export default {
	onLoad(val) {
		this.latitude = val.lat || 39.90960456049752;
		this.longitude = val.lng || 116.3972282409668;
		// this.currentLongitude = val.longitude
		this.markers = [
			{
				id: 1,
				latitude: this.latitude,
				longitude: this.longitude,
				iconPath: '../../static/icon/dingwei.png',
				// alpha: 0.1, //将图片设置为透明,通过开发者工具看不出效果,但真机是有效果的
				width: 17.5,
				height: 25,
				// callout: {
				// 	content: '拖动地图精准定位门店',
				// 	borderRadius: 20,
				// 	fontSize: 20
				// }
			}
		];
		var that = this;
		//获取系统信息
		uni.getSystemInfo({
			success: function(res) {
				that.$nextTick(function() {
					that.serviceHeight = res.windowHeight;
				});
			}
		});
	},
	onReady() {
		//铺满地图，实现点击
		// this.createMarkers()
		//地图中心点
		// this.markersMethoed()
	},
	data() {
		return {
			latitude: '',
			longitude: '',
			curentLatitude: null,
			currentLongitude: null,
			//铺满地图
			polygons: [],
			markers: [
				{
					id: 1,
					latitude: this.latitude,
					longitude: this.longitude,
					iconPath: '../../static/icon/zuob.png',
					alpha: 0.1, //将图片设置为透明,通过开发者工具看不出效果,但真机是有效果的
					width: 31,
					height: 44,
					callout: {
						content: '拖动地图精准定位门店',
						borderRadius: 20,
						fontSize: 20
					}
				}
			],
			//设备的高度
			serviceHeight: null,
			commitFlag: false
		};
	},

	methods: {
		//获取坐标跟经纬度
		createMarkers() {
			// this.mapCtx = wx.createMapContext('map')   @regionchange="regionchange"
			this.mapCtx = uni.createMapContext('map', this);
			const query = uni.createSelectorQuery();
			const map = query.select('#map').boundingClientRect();
			let that = this;
			that.mapCtx.getRegion({
				success(res1) {
					// console.log(res1)
					that.mapCtx.getScale({
						success(res2) {
							query.exec(res => {
								//获取地图的宽度
								let width = res[0].width;
								let height = res[0].height;
								let _markers = that.markers2(res1.northeast, res1.southwest, res2.scale, width, height);
								that.markers = _markers;
							});
						}
					});
				}
			});
		},
		//铺满的比例函数
		markers2(northeast, southwest, scale, width, height) {
			const markersize = 30;
			//北减南的经度、纬度
			const markerslng = ((northeast.longitude - southwest.longitude) * markersize) / width;
			const markerslat = ((northeast.latitude - southwest.latitude) * markersize) / height;

			const maxlon = northeast.longitude;
			const maxlat = northeast.latitude;
			const minlon = southwest.longitude;
			const minlat = southwest.latitude;

			const lons = this.range(minlon, maxlon, markerslng);
			const lats = this.range(minlat, maxlat, markerslat);

			let _markers = [];
			//双层循环得到正方形的区域
			lons.forEach((lon, i) => {
				lats.forEach((lat, j) => {
					_markers.push({
						id: lon + ',' + lat,
						latitude: lat,
						longitude: lon,
						iconPath: '../../static/icon/zuob.png',
						alpha: 0.1, //将图片设置为透明,通过开发者工具看不出效果,但真机是有效果的
						width: markersize,
						height: markersize
					});
				});
			});
			return _markers;
		},
		//得到区域从开始到结束每个覆盖点的坐标值
		range(start, edge, step) {
			for (var ret = []; (edge - start) * step > 0; start += step) {
				ret.push(start);
			}
			return ret;
		},
		//点击地图
		markertap(e) {
			console.log(e);
		},
		//移动地图
		// regionchange(e) {
		// 	this.mapCtx = uni.createMapContext('map', this);
		// 	var that = this;
		// 	//改变中心经纬度
		// 	that.mapCtx.getCenterLocation({
		// 		success(res1) {
		// 			that.latitude = res1.latitude;
		// 			that.longitude = res1.longitude;
		// 			// console.log(res1)
		// 			//地图中心点
		// 			// that.markersMethoed()
		// 			// setTimeout(() => {
		// 			// 	//铺满地图
		// 			// 	// that.createMarkers()、
		// 			//
		// 			// }, 200)
		// 		}
		// 	});
		// },
		//markers的位置方法
		markersMethoed() {
			const markersize = 30;
			this.markers = [
				{
					id: 1,
					latitude: this.latitude,
					longitude: this.longitude,
					iconPath: '../../static/icon/dingwei.png',
					// alpha: 0.1, //将图片设置为透明,通过开发者工具看不出效果,但真机是有效果的
					width: 31,
					height: 44,
					// callout: {
					// 	content: '拖动地图精准定位门店',
					// 	borderRadius: 20,
					// 	fontSize: 20
					// }
				}
			];
		},
		
	}
};
</script>

<style lang="less" scoped>
.mapDetail {
	height: 100%;
	width: 100%;
	.map_inner {
		position: relative;
		height: 100%;
		width: 100%;
		.map {
			width: 100%;
			height: 100%;
		}
		.myPerson_location {
			position: absolute;
			right: 20rpx;
			bottom: 160rpx;
			padding: 18rpx 20rpx;
			background-color: #ffffff;
			border-radius: 10rpx;
			align-items: center;
			display: flex;
			> image {
				width: 34rpx;
				height: 34rpx;
				margin-right: 10rpx;
			}
		}
		
		
	}
}
</style>
